import { Component, ViewChild  } from '@angular/core';
import { NavController, ViewController, Platform, ModalController, AlertController, ToastController } from 'ionic-angular';

import { ModalPage } from '../modal/modal'
import { Modal2Page } from '../modal2/modal2'
import { Observable } from 'rxjs/Observable';
import { NotesService } from '../../services/notes.service';
import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFireDatabase } from 'angularfire2/database';

import { Http } from '@angular/http';
import * as papa from 'papaparse';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  private notes: any = [
    {
   
      
    
  }
 ];

 private encabezados: any = [
  
    "Id Unico",
      "Anillo",
      "Nombre",
      "Palomar",
      "Color",
      "Id del Padre",
      "Anillo del padre",
      "Nombre del padre",
      "Id de la Madre",
      "Anillo de la Madre",
      "Nombre de la Madre",
      "No. de Vuelos",
      "No. de premios",
      "Observaciones",
    "Modificado por:"


];
  url: any;
  notedescargar:any = [
    { }
 ];





  constructor(public navCtrl: NavController, public viewCtrl : ViewController, public modalCtrl: ModalController, private platform:Platform,  public noteService:NotesService, public alertCtrl:AlertController, 
    private afAuth: AngularFireAuth, private toast: ToastController, public afDB: AngularFireDatabase ) {

      this.noteService.getNotes().valueChanges()
      .subscribe(notas=>{
            this.notes = notas;
            console.log(this.notes);
           

        } );


  }

  ionViewWillLoad(){
    
    this.afAuth.authState.subscribe(data => {
      if (data.email && data.uid){
          this.toast.create({
              message: "Inicio de sesión exitoso!",
              duration: 1000
          }).present();
      }
     

    }
    
       
    );


  }
  

  


        verpigeon(id) {

  
let modal = this.modalCtrl.create(ModalPage,{id:id} );



    
    modal.present();
    

  };
  

  nuevopigeon(){

    let modal = this.modalCtrl.create(ModalPage,{id:0} );
    modal.present();
  }

  acomodar(){
    this.notes.sort(function (a, b) {
      return a.anillo - b.anillo;
     
    });
   


  }
  downloadCSV() {

this.notedescargar=this.notes;
console.log(this.notedescargar);


    let csv = papa.unparse(
     
      this.notedescargar
        
    );
 
    
    var blob = new Blob([csv]);
    var a = window.document.createElement("a");
    a.href = window.URL.createObjectURL(blob);
    a.download = "Mis Pigeons.csv";
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
  }
 
  private handleError(err) {
    console.log('something went wrong: ', err);
  }
 
  





}



