import { Component, ViewChild } from '@angular/core';
import {  NavController, ModalController, LoadingController, NavParams, ViewController, ToastController, AlertController, ActionSheetController, Platform } from 'ionic-angular';

import { Modal4Page } from '../modal4/modal4'

import { Observable } from 'rxjs/Observable';
import { NotesService } from '../../services/notes.service';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { AngularFireStorageModule, AngularFireStorage, AngularFireUploadTask } from 'angularfire2/storage';

import * as firebase from 'firebase';
import { InAppBrowser, InAppBrowserOptions } from '@ionic-native/in-app-browser';
/**
 * Generated class for the ModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-modal',
  templateUrl: 'modal.html',
})
export class ModalPage {

  counter: any;
  event: any;
  note: any = {
     
      id: null,
      anillo: null,
      nombre: null,
      palomar: null,
      color: null,
      padreid: null,
      padreanillo:null,
      padrenombre:null,
      madreid: null,
      madreanillo:null,
      madrenombre:null,
      vuelos:null,
      premios:null,
      observaciones:null,
      modificado:null
      
    
  };
  id : any = null;
  show=true;

  lineas: any = {
     
    id: null,
    idpalomapadre: null,
    nombrepalomapadre: null,
    anillopaloma1:null,
    
    idpalomamadre: null,
    nombrepalomamadre: null,
    anillopalomamadre:null,
    

   

   
    
  
};
  fotodx: any;
  cargado=false;
cargadoekg=false;
cargadorx=false;
preview=false;
previewekg=false;
previewrx=false;
someTextUrl ='';
someTextUrlekg ='';
someTextUrlrx ='';
iaboptions: InAppBrowserOptions = {
  location: 'no',
  zoom: 'yes'
  

};

  selectedPhoto;
  loading;
  currentImage;



  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl : ViewController,  
    public notesService : NotesService, public toastCtrl:ToastController, public alertCtrl:AlertController, public actionSheetCtrl: ActionSheetController,
    public platform: Platform, private camera: Camera, private afStorage: AngularFireStorage, private iab:InAppBrowser,
    public loadingCtrl: LoadingController,  public modalCtrl: ModalController) {

      this.id = navParams.get('id');
      if (this.id != 0){
      notesService.getNote(this.id)
      .valueChanges().subscribe(note=> {
        
        this.note = note});

        this.cargado=true;
    this.cargadoekg=true;
    this.cargadorx=true 
      };

      this.getSomeText(); 


  }

  getSomeText() {

    firebase.storage().ref('images/'+this.id+'/').child('uploaded.png').getDownloadURL()
      .then(response => {this.currentImage = response; 
                         this.preview=true } )
      .catch(error => console.log('error', error)
      
      

    );
    

     

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ModalPage');
  }

  dismiss()
  {

    this.viewCtrl.dismiss();
  }

  selectpigeona(){
    let modal = this.modalCtrl.create(Modal4Page);

    modal.onDidDismiss((notes) => {
      console.log(notes);
      this.note.padreid=notes.id;
      this.note.padrenombre=notes.nombre;
      this.note.padreanillo=notes.anillo;
        

    });

    
    modal.present();

  }
  selectpigeonb(){
    let modal = this.modalCtrl.create(Modal4Page);

    modal.onDidDismiss((notes) => {
      console.log(notes);
      this.note.madreid=notes.id;
      this.note.madrenombre=notes.nombre;
      this.note.madreanillo=notes.anillo;
        

    });

    
    modal.present();

  }
  verpigeonpadre(padreid) {

  
    let modal = this.modalCtrl.create(ModalPage,{id:padreid} );
    
    
    
        
        modal.present();
        
    
      };
  verpigeonmadre(madreid) {

  
    let modal = this.modalCtrl.create(ModalPage,{id:madreid} );
    
    
    
        
        modal.present();
        
    
      };

  capturarpigeon() {
     
    const options: CameraOptions = {
      quality: 100,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      saveToPhotoAlbum:true,
      allowEdit: true
      
    }
    this.camera.getPicture(options).then((imageData) => {
      // imageData is either a base64 encoded string or a file URI
      this.loading = this.loadingCtrl.create({
        content: 'Espere un momento...'
      });
      this.loading.present();

      this.selectedPhoto  = this.dataURItoBlob('data:image/jpeg;base64,' + imageData);

      this.upload();
    }, (err) => {
      console.log('error', err);
    });
      // If it's base64:
     
      
     
      

     
     (err) => {
      // Handle error
      console.log(err);
      let toast = this.toastCtrl.create({
        message: 'No se pudo tomar la foto',
        duration: 2000,
        position: 'middle'
      });
      toast.present();
     };
    
    }

    dataURItoBlob(dataURI) {
      // code adapted from: http://stackoverflow.com/questions/33486352/cant-upload-image-to-aws-s3-from-ionic-camera
      let binary = atob(dataURI.split(',')[1]);
      let array = [];
      for (let i = 0; i < binary.length; i++) {
        array.push(binary.charCodeAt(i));
      }
      return new Blob([new Uint8Array(array)], {type: 'image/jpeg'});
    };

    upload() {
      if (this.selectedPhoto) {
        var uploadTask = firebase.storage().ref().child('images/'+this.id+'/uploaded.png').put(this.selectedPhoto);
        uploadTask.then(this.onSuccess, this.onError);
      }
    }

    onSuccess = (snapshot) => {
      this.currentImage = snapshot.downloadURL;
      this.loading.dismiss();
    }
  
    onError = (error) => {
      console.log('error', error);
      this.loading.dismiss();
    }

  

  

  
  


  
 
 

  opcionespigeon(){
    if (this.id != 0){
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Opciones',
      cssClass: 'action-sheets-basic-page',
      buttons: [
        
        {
          text: 'Guardar',
          icon: !this.platform.is('ios') ? 'md-add-circle' : null,
          handler: () => {
            console.log('Share clicked');
              if (this.id != 0){
                this.notesService.editarpac (this.note);
                let toast = this.toastCtrl.create({
                  message: 'Entrada modificada',
                  duration: 2000,
                  position: 'top'
                });
                toast.present();
               
              }
              else{
          
              this.note.id = Date.now ();
              this.notesService.crearpac (this.note);
              let toast = this.toastCtrl.create({
                message: 'Pigeon agregado',
                duration: 2000,
                position: 'top'
              });
              toast.present();
               }
              this.viewCtrl.dismiss();
              
            
          }
        },
        {
          text: 'Borrar Pigeon',
          role: 'destructive',
          icon: !this.platform.is('ios') ? 'trash' : null,
          handler: () => {
            console.log('Delete clicked');
            let alert = this.alertCtrl.create({
              title: 'Atención',
              subTitle: 'Se borrará el Pigeon de manera permanente',
              buttons: [
               
                {
                  text: 'Aceptar',
                  handler: () => {
                    this.show =false
                    this.notesService.borrarpac(this.note);
                    this.viewCtrl.dismiss();
                    let toast = this.toastCtrl.create({
                      message: 'Pigeon borrado',
                      duration: 2000,
                      position: 'top'
                    });
                    toast.present();
                    
                  }
                },
                {
                  text: 'Cancelar',
                  handler: () => {
                      this.viewCtrl.dismiss();
                  }
                },
        
        
               ]
            });
            alert.present();
         
          }
        },
        
        {
          text: 'Cancelar',
          role: 'cancel', // will always sort to be on the bottom
          icon: !this.platform.is('ios') ? 'close' : null,
          handler: () => {
            console.log('Cancel clicked');
            
          }
        }
      ]
    });
    actionSheet.present();
 }
else {
  let actionSheet = this.actionSheetCtrl.create({
    title: 'Opciones',
    cssClass: 'action-sheets-basic-page',
    buttons: [
      
      {
        text: 'Guardar',
        icon: !this.platform.is('ios') ? 'md-add-circle' : null,
        handler: () => {
          console.log('Share clicked');
            if (this.id != 0){
              this.notesService.editarpac (this.note);
              let toast = this.toastCtrl.create({
                message: 'Pigeon modificado',
                duration: 2000,
                position: 'top'
              });
              toast.present();
             
            }
            else{
        
            this.note.id = Date.now ();
            this.notesService.crearpac (this.note);
            let toast = this.toastCtrl.create({
              message: 'Pigeon agregado',
              duration: 2000,
              position: 'top'
            });
            toast.present();
             }
            this.viewCtrl.dismiss();
            
          
        }
      },
      
      
      {
        text: 'Cancelar',
        role: 'cancel', // will always sort to be on the bottom
        icon: !this.platform.is('ios') ? 'close' : null,
        handler: () => {
          console.log('Cancel clicked');
          
        }
      }
    ]
  });
  actionSheet.present();



}
}





}



